@use 'sass:math';
/************ Basic *************/

body {
  font-family: $sans;
  color: $white;
  -webkit-font-smoothing: antialiased; /* Fix for webkit rendering */
  -moz-osx-font-smoothing: grayscale; /* Fix for Mozilla */
  text-size-adjust: 100%;
  background: $grey-dark;
  min-height: 100%;
  height: 100%;
  overflow-x: hidden;
}

footer {
  margin: $space-m auto;
  margin-left: $space-m;
  margin-right: $space-m;


  animation: fadeIn 0.6s $animation-easing;
}


::selection {
  background: $black-real;
  color: $color-highlight;
}

.maxwidth {
  margin: 0 auto;
  float: left;
  width: 100%;

  p {
    max-width: 80%;
  }
}

/* Container
   ========================================================================== */
.container {
  overflow-x: hidden;
  animation: fadeIn 0.6s $animation-easing;


  /* padding rand */
  h1, h2, h3, h4, h5, p, ul, ol {
    text-align: left;
  }

  h1, h2, h3, h4 {
    font-family: $font-medium;
  }

  &.maxwidth {
    margin: 0 auto;

    h1, h2, h3, h4, h5, p, ul, ol {
      //margin-left: $space-m;
      //margin-right: $space-m;
    }

  }
}

.add-bottom-margin {
  margin-bottom: $space-s;
}

.add-bottom-margin-extra {
  margin-bottom: $space-xxl;
}

.description {
  animation: fadeIn 0.6s $animation-easing;

  div {
    width: 100%;
  }

  .list {
  }
}

.list {
  width: 50%;
  float: left;
  display: inline-block;
  padding-right: $space-m;
  margin-bottom: $space-m;
}

.post-list {
  h2 {
    margin: $space-xs 0;
  }
}

.imagelist {
  margin: $space-m 0;
  user-select: none;
  animation: fadeIn 0.6s $animation-easing;
}


.margin-m {
  margin: $space-l;
  @media only screen and (max-width: $breakpoint-small-max-width) {
    margin: $space-xs;
  }
}

.taglist {
  text-align: left;
  width: 100%;
  margin-top: $space-xs;
  font-size: $font-xs;
  line-height: $font-s;
  display: block;
  text-transform: lowercase;
}

.experiences-list {
  text-align: left;
  width: 100%;
  display: block;
  float: left;
}

.description {
  font-size: $font-xs;
  color: $grey-medium;
  height: auto;
  margin-bottom: $space-s;
  display: block;
  float: left;
}

picture, image, img {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

picture {
  display: block;
  margin-bottom: $space-xxxl;
}

.coversplasher {
  picture {
    margin-bottom: $space-xxl;
  }

  animation: fadeIn .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;

  .video-container {
    margin-bottom: 0;
  }

}


/* MaxWidth / Nav
   ========================================================================== */
.navigation,
.container,
.description,
footer {
  margin: 0 auto;
  float: none !important;
  max-width: 66%;

  display: block;
  padding-bottom: 4rem;
  overflow: hidden;

}

.navigation {
  margin-bottom: $space-xl2;
  z-index: 999;
}

footer {
  margin-bottom: $space-m;
}



.navigation {
  &.reduced-true {
    padding-bottom: 0;
    margin-bottom: 0;

    position: fixed;
    left: 17%;
  }
}

/* LOGO */
/* Logo
   ========================================================================== */


.logo {
  font-size: $font-l;
  margin-top: $space-s;
  animation: fadeIn 0.3s $animation-easing;
  width: $flow-dot-size;
  height: $flow-dot-size;
  position:fixed;
  z-index:9999;
  a {
    text-decoration: none;
    color: #e8e8e8;
  }

  p {
    color: $grey-light;
    opacity: 0.3;
  }
  .block {
    width: $flow-dot-size;
    height: $flow-dot-size;
    background-color: $black-real;
    color:transparent;
    display:block;
    line-height: $flow-dot-size;
    color:transparent;
  }
  .block:hover {
    color:transparent;
  }
}
.logo:hover {
  background-color:$white-real;
  transition: background-color 0.3s;
  .block {
    color:transparent;
      background-color:$white-real;
  }
}
/* Main
   ========================================================================== */
.main {
  .splash-fix {
    top:62vh;
      position:relative;
  }
  footer {
    margin-top:62vh;
  }
}
@media (max-width: $breakpoint-small-max-width) {
  .main {
    .splash-fix {
      top:43vh;
      position:relative;
    }
  }
}

/* Project
   ========================================================================== */


.project {
  float: left;
  width: 100%;

  &.container {
    overflow: hidden;
  }

  ul {
    padding-inline-start: $space-s;
    list-style: disc;
  }

  li {
    color: $white;
    list-style: disc;
  }

}

/* schwarzer punkt */
.flow {
  border:none;
  z-index: 99;
  width: $flow-dot-size;
  height: $flow-dot-size;
  display: block;
  position: fixed;
  top: 23.8vh;
  left: 5.8vw;
  border-radius: 50%;
  background: black;

  .button,
  .button-next {
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    border-radius: 50%;
    width: $flow-dot-size;
    height: $flow-dot-size;
   

  }
  .button:hover {
    border-radius: 50%;
     border:2px $color-highlight;
  }  
}
.flow:hover {
  background:transparent;
}

/* readmore
   ========================================================================== */
.readmore {
  cursor: pointer;
  margin-bottom: $space-s;

  font-size: $font-xs;
}


.hidden-text {
  height: 0px !important;
  //transition: height 0.9s;
  //animation: fadeOut 0.3s $animation-easing;
  opacity: 0;
}

#moretext {
  //height: auto;
  min-height: 0px;
  transition: height 0.3s;
  //animation: fadeIn 0.3s $animation-easing;
  margin-bottom: $space-s;
}

/* Emoji
   ========================================================================== */
.bigemo {
  font-size: $font-xxxl;
  line-height: $line-xxxl;
}

/* Code Format
   ========================================================================== */


code {
  color: $accent;
  font-family: "Monaco", "Courier New", "Courier";
  font-size: .75em;
  font-weight: bold;
}

.highlight {
  background: $black;
  padding: 12px;
  margin-bottom: $lh;
  overflow-x: scroll;

  code {
    color: $white;
    font-family: "Monaco", "Courier New", "Courier";
    font-size: .9em;

    .lineno {
      margin-right: math.div($lh, 2);
      color: $grey-light;
      line-height: 2em;
    }

    .nt {
      color: #c85b5c;
    }

    .na {
      color: #f0e687;
    }

    .s {
      color: #87ceeb;
    }
  }
}

hr {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: 4px;
  background: $grey-light;
  border: none;
  margin-bottom: $lh;
  border-radius: 2px;
}

input {
  background: none;
  border: none;
  outline: none;
}

/* Button
   ========================================================================== */
.button {
  background-color: $link;
  text-align: center;
  line-height: $font-xl;
  margin-bottom: $space-l;
  margin-top: $space-l;
  padding: $space-m 0;
  cursor: pointer;
  display: block;
  font-size: $font-xxl;
  line-height: $font-xxl;

  &.big {
    font-size: $font-xxl;
    line-height: $font-xxl;
  }

  transition: background-color 0.3s;

  &:hover, &:hover > a, &:hover > a:visited {
    background-color: transparent;
    color: $white !important;
    transition: background-color 0.2s;
  }

  span {
    display: inline-block;
    width: auto;

    margin-bottom: $space-xs;

    &.label-next {
      text-decoration: none !important;
    }

    &.label-target {
      font-family: $font-medium;
      text-decoration: underline;
    }
  }

  &.small {
    font-size: $font-xs;
    line-height: $line-s;
    margin-bottom: $space-s;
    margin-top: $space-s;
    padding: $space-xs $space-xs;
    display: inline-block;

    margin: $space-s 0 0 0;
    margin-right: $space-s;
    padding: 0;

    &:hover, &:hover > a, &:hover > a:visited {
      background-color: transparent;
      color: $link !important;
      transition: background-color 0.2s;
    }
  }

}


/* Modes
   ========================================================================== */
.hidden {
  display: none !important;
}

/* Video
   ========================================================================== */
video {
  width: 100%;
  display: block;
  animation: fadeIn 0.9s $animation-easing;
}


/* brandy
   ========================================================================== */
.clients-brands-etc {
  display: block;
}

/*
PANO
 */
.panolens-container {
  position: fixed;
  top: 0;
  left: 0;
  animation: fadeIn 0.3s $animation-easing;

  &.hidden {
    pointer-events: none;
  }
}


.panolens-splash {

}

.close {
  position: fixed;
  top: $space-xs;
  right: $space-xs;
  width: $space-s;
  height: $space-s;
  background-color: white;
  padding: auto 0;
  text-align: center;
  line-height: $space-s;
  cursor: pointer;
}


/* Imprint etc.
   ========================================================================== */
.single {
  font-size: $space-xss;
  line-height: $space-s;

  p {
    font-size: $space-xss;
    line-height: $space-s;
  }

  .small-text {
    font-size: $space-xss;
    line-height: $space-s;
    color: $grey-medium;
  }

  li {
    color: $grey-medium;
    font-size: $space-xss;
    line-height: $space-s;
  }

  strong {
    padding-top: $space-s;
    color: $white;
    display: block;
  }
}


/* NOTES etc.
   ========================================================================== */
.note-glazed
{
  display:block;
  width:100vw;
  height:10vw;
  color: $white;
  background-color:$ultra-pink !important;
  z-index:999;
  text-align:center;
  padding:2vw;
  font-size:$font-xl;
  line-height:$line-l;

}


/* Tablet
   ========================================================================== */
@media only screen and (max-width: $breakpoint-medium-max-width) {
  .list {
    width: 100%;
  }

}

/* Mobile
   ========================================================================== */
@media (max-width: $breakpoint-small-max-width) {
  body, html, p, li, a, h2, h3, h4 {
    font-size: $font-s;
    line-height: $line-s;
  }
  h1 {
    font-size: $font-xl;
    line-height: $line-xl;
    word-break: normal;
  }
  h2 {
    font-size: $font-l;
    line-height: $line-l;
  }

  .logo {
    font-size: $font-m;
    width:$flow-dot-size-mobile;
    height:$flow-dot-size-mobile;

    .block {
      width:$flow-dot-size-mobile;
      height:$flow-dot-size-mobile;
    }
  }
  .flow,
  .flow .button {
      width:$flow-dot-size-mobile;
      height:$flow-dot-size-mobile;
  }
  .flow:hover {
      background-color:$yellow_botanics;
  }

  .post {
    @include column(9);
    max-width: 420px;
    display: block;
    margin: 0 auto;
    float: none;
  }

  footer {
    margin: $space-s auto;
    margin-left: $space-s;
    margin-right: $space-s;
    padding-bottom: $space-l;
    display: block;
  }

  small {
    font-size: $font-xs;

    a {
      font-size: $font-xs;
    }
  }
  .maxwidth {
    text-align: left;
    //margin-left: $space-s;
    //margin-right: $space-s;
  }
  .container {

    &.maxwidth {
      margin: 0 auto;

      h1, h2, h3, h4, h5, p, ul, ol {

      }
    }

  }


  .readmore a {
    font-size: $font-xs;
  }

  .button {
    margin-left: 0;
    margin-right: 0;
    padding: $space-s;

    span {
      text-align: left;
      display: block;
      width: auto;

      line-height: $font-xxl;

    }
  }
  .button-next {
    a {
      font-size: $font-xl;
      text-decoration: none;
    }

  }


  .list {
    width: 100%;
  }

  picture {
    margin-bottom: $space-xl !important;
  }

  .coversplasher {
    picture {
      margin-bottom: $space-xl;
      min-height:50vh;
    }
    img {
     min-height:50vh;
    }

    .video-container {
      margin-top: $space-xl;
    }
  }


  video {
    margin-bottom: $space-xl;
  }

  // fixing
  .navigation,
  .container,
  .description,
  footer {
    max-width: 88%;
    width: 88%;
  }
  .navigation {
    &.reduced-true {

      left: 6%;
      text-align: left;
      width: 88%;
    }
  }
}

// Fixings for BigScreens?
@media only screen and (min-width: 1920px) {

  .container {
    h1, h2, h3, h4, h5, p {
    }
  }

  picture {
    //max-width: 1920px;
  }
  .imagelist {
    picture {
      // max-width: 1920px;
    }
  }

}
