/* ==========================================================================
   TYPE
   - Meat
   - Headings
   - Media Query adjustments
   ========================================================================== */

/* Meat
   ========================================================================== */

p {
  font-size: $font-m;
  line-height: $line-l;
  padding-bottom: $font-xxl;
}

.intro {
}

ul, ol {
  padding-bottom: $font-m;
  list-style-position: inside;
  padding-inline-start: 0;
}

li, .li,
footer li {
  font-size: $font-l;
  line-height: $line-xl;
  list-style: none;
  padding: initial;
  color: $grey-medium;
}

.task li,
.list li,
.post-list li {
  padding-bottom: $space-xs;
}

.task li:before {
  content: '*';
  margin-right: $space-xxs;
  color: $grey-dark;
}

/* Headings
   ========================================================================== */

h1, .h1 {
  font-size: $font-xxl;
  line-height: $line-xl;

  margin-top: $space-xs !important;
  margin-bottom: $space-s !important;
}

h2, .h2 {
  font-size: $font-xl;
  line-height: $line-xl;
  margin-top: $space-xs !important;
  margin-bottom: $space-xs !important;
}

h3, .h3 {
  font-size: $font-m;
  line-height: $line-m;
}

h4, .h4 {
  font-size: $font-s;
  line-height: $line-s;

}

h5, .h5 {
  font-size: $font-xs;
  line-height: $line-xs;

}

h6, .h6 {
  font-size: $font-xxs;
  line-height: $line-xs;
}

a, a:active, a:visited {
  color: $link;
  text-decoration: underline;
  //border-bottom: solid 2px $link;
  //display: inline;
  //padding-bottom: 3px;

}

a:hover {
  color: $accent;
}

.dark {
  .container {
    h2, h4 {
      color: $link_light;
    }
  }

  a {
    color: $link_light;
  }

  a:hover {
    color: $link;
    // $edgebomberpink;
  }
}

.edgebomberpink {
  background-color: $edgebomberpink;

  .logo p, .description {
    color: rgb(255, 160, 158);
  }

  a:hover {
    color: $black;
  }
}

.yellowbotanics {
  background-color: $yellow_botanics;
  color: $black;

  .logo, .logo a,
  .logo p, .description {
    color: $edgebomberpink;
  }

  a {
    color: $black;
  }

  a:hover {
    color: $edgebomberpink;
  }

}

.feature-light {
  background-color: #c4c4c4;
  color: black;

  a {
    color: black;
  }

  a:hover {
    color: $accent;
  }

  .post-list li a {
    color: $grey-medium;
  }
}

.white {
  background-color: $white-broke;
  color: $black;

  .project li {
    color: $black;
  }

  a {
    color: $black;
  }

  a:hover,
  .post-list li a:hover {
    color: $link-hover-extreme;
  }

  .post-list li a {
    color: $link;
  }
}

.black {
  color: $white-real;
  background-color: $black-real;
}

.fourzerofour {
  background: #fc1111;

  .logo {
    a {
      color: #881701;
    }

    p {
      color: #d01701;
    }
  }
}

.aimerica {
  background-color: $aimerica;

  .logo p, .description {
    color: rgb(255, 160, 158);
  }

  a:hover {
    color: $black;
  }
}
.dsp {
  background-color: $color-dsp;
  &.no-pic-margin
  {
    .imagelist {
      picture { margin-bottom:0 !important; }
    }
  }
  .logo, .logo .block {
    background-color:$white-real;
  }
  .logo:hover,
  .logo .block:hover,
  .logo .button:hover {
    background-color:$link-hover-extreme;
  }
  a {
    color:$link;
  }
  a:hover {
    color:$link-hover-extreme;
  }
}

.meta {
}

blockquote, blockquote p {
}

.archive input {
}


