/* ==========================================================================
   VARIABLES
   ========================================================================== */

/* Colors
   ========================================================================== */

$black: #3b3b3b;
$black-real: #000000;
$white: #f9f9f9;
$white-real: #ffffff;
$white-broke: #f1f1f1;

$grey-dark: #222;
$grey-medium: rgba(95, 95, 95, 1);
$grey-light: #d9d9d9;

$hilite: #fffbd7;
$color-highlight: #fffe39;

$link: #df2000; /*#fc1111;*/
$link-hover: #df2000; /*#fc1111;*/
$link-hover-extreme: #0002fd;
$link_dark: rgb(8, 8, 8); // rgba(252, 255, 242, 0.55);
$link_dark_rgb: #030303;
$link_light: #fefefe;

$accent: $black; // #24536B;
$accent_dark: $black; // #21d5ff;

$edgebomberpink: #D22F68; // #D53D72;
$yellow_botanics: #F9E14C; // #f1a717;
$ultra-pink: #ff7caaw;

$aimerica: #2d0233;
$color-dsp: #0a0a0a;

/* buttons flow */
$flow-dot-size: 4.18vw;
$flow-dot-size-mobile: 10.36vw;
/* Typography
   ========================================================================== */
$sans: "Circ-Book", "Source Sans Pro", "Menlo", monospace, mono, sans-serif;


/* Type Scale
   ========================================================================== */

// Line Height
$lh: 33px;
$lhsmall: 24px;

// Sizing
$footerHeight: 160px;

// Griddit
/*
Fonts Sizes
*/
$font-xxs: 0.75rem; // 12px
$font-xs: 0.875rem; // 14px
$font-default: 1.0rem; // 16px
$font-s: 1.125rem; // 18px
$font-m: 1.5rem; // 24px
$font-l: 1.75rem; // 28px
$font-xl: 2.25rem; // 36px
$font-xxl: 3rem; // 48px
$font-xxxl: 6rem;

/*
Line Heights
*/
$line-xxs: 1.125rem; // 18px
$line-xs: 1.5rem; // 24px
$line-s: 1.75rem; // 28px
$line-m: 2.25rem; // 36px
$line-l: 2.375rem; // 38px
$line-xl: 3rem; // 48px
$line-xxl: 4.375rem; // 70px
$line-xxxl: 6.75rem; // 70px
/*
Spacing
*/
$space-xxxs: 0.28rem; // 5px
$space-xxs: 0.3125rem; // 5px
$space-xs: 0.625rem; // 10px
$space-xss: 1.25rem; // 30px
$space-s: 1.875rem; // 30px
$space-m: 3.75rem; // 60px
$space-l: 5.625rem; // 90px
$space-xl: 6.25rem; // 100px
$space-xl2: 12.5rem; // 200px
$space-xxl: 17.5rem; // 240px
$space-xxxl: 21.25rem; // 300px
$space-xxxxl: 27.25rem;


/* Break Points
   ========================================================================== */

/* Mobile */
$breakpoint-small-max-width: 520px;

/* Tablet */
$breakpoint-medium-max-width: 800px;
$breakpoint-large-max-width: 1200px;

/* Style Tidbits
   ========================================================================== */
$borderR: 4px;

