$circtype-font-src: '/3a29/assets/fonts/circ';

@font-face {
  font-display: auto;
  font-family: 'Circ-Book';
  src: url($circtype-font-src + '/circ-book-s.eot'); // IE9 Compat Modes
  src: url($circtype-font-src + '/circ-book-s.eot?#iefix') format('embedded-opentype'), // IE6-IE8
  url($circtype-font-src + '/circ-book-s.woff') format('woff'), // Pretty Modern Browsers
  url($circtype-font-src + '/circ-book-s.ttf') format('truetype'), // Safari, Android, iOS
  url($circtype-font-src + '/circ-book-s.svg#circ-book-s') format('svg'); // Legacy iOS
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-display: auto;
  font-family: 'Circ-Medium';
  src: url($circtype-font-src + '/circ-medium-s.eot'); // IE9 Compat Modes
  src: url($circtype-font-src + '/circ-medium-s.eot?#iefix') format('embedded-opentype'), // IE6-IE8
  url($circtype-font-src + '/circ-medium-s.woff') format('woff'), // Pretty Modern Browsers
  url($circtype-font-src + '/circ-medium-s.ttf') format('truetype'), // Safari, Android, iOS
  url($circtype-font-src + '/circ-medium-s.svg#circ-medium-s') format('svg'); // Legacy iOS
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-display: auto;
  font-family: 'Circ-Bold';
  src: url($circtype-font-src + '/circ-bold-s.eot'); // IE9 Compat Modes
  src: url($circtype-font-src + '/circ-bold-s.eot?#iefix') format('embedded-opentype'), // IE6-IE8
  url($circtype-font-src + '/circ-bold-s.woff') format('woff'), // Pretty Modern Browsers
  url($circtype-font-src + '/circ-bold-s.ttf') format('truetype'), // Safari, Android, iOS
  url($circtype-font-src + '/circ-bold-s.svg#circ-bold-s') format('svg'); // Legacy iOS
  font-weight: 700;
  font-style: normal;
}


/*
Fonts
*/
$font-bold: 'Circ-Bold', Helvetica, Verdana, sans-serif;
$font-medium: 'Circ-Medium', Helvetica, Verdana, sans-serif;
$font-normal: 'Circ-Book', Helvetica, Verdana, sans-serif;
