.personal {

  padding-top: $space-l;

  h4 {
    text-transform: uppercase;
    letter-spacing: 0.05rem;
    margin-bottom: $space-m;
  }

  hr {
    margin: $space-xxl;
    height: 1px;
    background-color: $grey-medium;
  }

  li {
    color: $grey-light;
    padding-bottom: $space-xs;
  }

  .maxwidth- {
    max-width: 80%;
  }

  @media only screen and (max-width: $breakpoint-small-max-width) {
    .maxwidth- {
      max-width: 90%;
    }
  }

  .date {
    width: 20%;
    display: inline-block;
    vertical-align: top;
    @media only screen and (max-width: $breakpoint-small-max-width) {
      font-size: $font-xs;
    }
  }

  .detail {
    width: 80%;
    display: inline-block;
  }

}
