/**
 * Animation & Transitions
 */
$animation-easing: cubic-bezier(0.75, 0.02, 0.5, 1) !default;


@keyframes fadeInUp {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate3d(0, 0.625rem, 0);
    transform: translate3d(0, 0.625rem, 0);
  }
  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

/*
// ------------------------------------------------------------------------ FadeInUp
*/

@keyframes fadeInLeft {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate3d(-0.625rem, 0, 0);
    transform: translate3d(-0.625rem, 0, 0);
  }

  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate3d(0, 0.625rem, 0);
    transform: translate3d(0, 0.625rem, 0);
  }

  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeInUpLong {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate3d(0, 5rem, 0);
    transform: translate3d(0, 5rem, 0);
  }

  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

.fade-up {
  opacity: 0;
  animation: fadeInUp .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

.fade-up-long {
  opacity: 0;
  animation: fadeInUp .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

.animation-delay-1s {
  -webkit-animation-delay: 1s;
  -moz-animation-delay: 1s;
  -o-animation-delay: 1s;
  animation-delay: 1s;
}

.animation-delay-500ms {
  -webkit-animation-delay: 500ms;
  -moz-animation-delay: 500ms;
  -o-animation-delay: 500ms;
  animation-delay: 500ms;
}


/*
// ------------------------------------------------------------------------ FadeDown
*/
@keyframes fadeInDown {
  from {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate3d(0, -0.625rem, 0);
    transform: translate3d(0, -0.625rem, 0);
  }

  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeDown {
  from {
    visibility: visible;
    -webkit-transform: translate3d(0, -0.625rem, 0);
    transform: translate3d(0, -0.625rem, 0);
  }

  to {
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

.fade-down {
  opacity: 0 !important;
  animation: fadeDown .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

/*
// ------------------------------------------------------------------------ FadeIn
*/
@keyframes fadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }

  to {
    opacity: 1;
    visibility: visible;
  }
}

.fade-in {
  opacity: 0;
  animation: fadeIn .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

.fade-in-slow {
  opacity: 0;
  animation: fadeIn 1.6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

@keyframes fadeInFlex {
  from {
  }

  to {
    opacity: 1;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}


@keyframes fadeInToHalf {
  from {
    opacity: 0;
    visibility: hidden;
  }

  to {
    opacity: 0.5;
    visibility: visible;
    -webkit-transform: none;
    transform: none;
  }
}

/*
// ------------------------------------------------------------------------ FadeOut
*/
@keyframes fadeOut {
  from {
    opacity: 1;
    visibility: visible;
  }

  to {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: none;
    transform: none;
  }
}

.fade-out {
  opacity: 0;
  animation: fadeOut .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

.fade-out-slow {
  opacity: 0;
  animation: fadeOut 1.6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

@keyframes fadeOutTop {
  from {
    opacity: 1;
    visibility: visible;
  }

  to {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: none;
    transform: none;
    top: -50vh;
  }
}

.fade-out-top {
  opacity: 0;
  animation: fadeOutTop .6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

.fade-out-slow-top {
  opacity: 0;
  animation: fadeOutTop 1.6s cubic-bezier(0.75, 0.02, 0.5, 1) 0s forwards;
}

/*
// ------------------------------------------------------------------------ Delays
*/

.animationDelay-1 {
  animation-delay: 1s;
}

.animationDelay-2 {
  animation-delay: 2s;
}


/*
Preload
 */
@keyframes preloading {
  0% {
    /*
    opacity: 0.7;
    */
    background-color: rgba(0, 0, 0, 0.5);
  }
  50% {
    /*
    opacity: 1.0;
    */
    background-color: rgba(0, 0, 0, 0.9);
  }
  100% {
    /*
    opacity: 0.7;
    */
    background-color: rgba(0, 0, 0, 0.5);
  }
}

/*
Texting
 */
@keyframes textingloop {
  0% {
    opacity: 0.1;
    background-color: $color-highlight;
  }
  20% {
    opacity: 1.0;
  }
  22% {
  }
  70% {
    opacity: 1.0;
  }
  100% {
    opacity: 0.1;

    background-color: rgba(0, 0, 0, 0.1);
  }
}

.textingloop {
  animation: textingloop 1.3s cubic-bezier(0.75, 0.02, 0.5, 1) 0s infinite;
}

@keyframes textingloopLight {
  0% {
    opacity: 0.1;
    background-color: $color-highlight;
  }
  23% {
    opacity: 1.0;
  }
  70% {
    opacity: 1.0;
  }
  100% {
    opacity: 0.1;
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.textingloopLight,
.bg-light .textingloop {
  animation: textingloopLight 1.3s cubic-bezier(0.75, 0.02, 0.5, 1) 0s infinite;
}


@keyframes typedjsNEWBlink {
  0% {
    opacity: 1.0;
    content: ' . ';
    background-color: $white;
  }
  50% {
    opacity: 0.0;
    background-color: $color-highlight;
    content: ' - ';
  }
  100% {
    opacity: 1.0;
    background-color: $white;
    content: ' - ';
  }
}

.typedjsNEWBlink {
  animation: typedjsNEWBlink 1.3s cubic-bezier(0.75, 0.02, 0.5, 1) 0s infinite;
}

@keyframes typedjsNEWBlinkOnLight {
  0% {
    opacity: 1.0;
    content: ' - ';
    background-color: $black;
  }
  50% {
    opacity: 0.0;
    background-color: $color-highlight;
    content: ' \ ';
  }
  100% {
    opacity: 1.0;
    background-color: $black;
    content: ' / ';
  }
}

.typedjsNEWBlinkOnLight,
.bg-light .typedjsNEWBlink {
  animation: typedjsNEWBlinkOnLight 1.3s cubic-bezier(0.75, 0.02, 0.5, 1) 0s infinite;
}
